/* eslint-disable */
<template>
<div>
    <h3>User</h3>
    <div class="container">
        <div v-if="result === null || undefined">
          <div
            class="alert alert-danger"
          >
            No user found
          </div>
        </div>
      <form @submit.prevent v-if="result">
        <fieldset class="form-group">
          <label>First Name</label>
          <input type="text" class="form-control" required :value="result.first_name" @input="event => result.first_name = event.target.value"/>
        </fieldset>
        <fieldset class="form-group">
          <label>Last Name</label>
          <input type="text" class="form-control" required :value="result.last_name" @input="event => result.last_name = event.target.value" />
        </fieldset>
        <fieldset class="form-group">
          <label>Email Id</label>
          <input type="text" class="form-control" required :value="result.email_id" @input="event => result.email_id = event.target.value"/>
        </fieldset>
        <button class="btn btn-success" @click="updateUser()">Save</button>
        <router-link class="btn btn-warning" :to="{ path: '/users'}">Back</router-link>
      </form>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue'
import { useRoute } from 'vue-router'

const route = useRoute()

let result = ref([])
let errors = ref([])


onMounted(()=>{
  const id = route.params.id
  fetch('http://localhost/api/'+id)
    .then(response => {
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    return response.json();
  })
  .then(data => {
    console.log(data)
    result.value = data
  })
    .catch(error => {
    console.error('There was a problem with the fetch operation:', error);
  });
})

const updateUser = (()=> {
 const id = route.params.id
 console.log(result.value.first_name)
 const body = {
  first_name : result.value.first_name,
  last_name : result.value.last_name,
  email_id : result.value.email_id
 }
  fetch('http://localhost/api?id='+ id, {
  method: 'PUT',
  headers: {
    'Content-Type': 'application/json'
  },
  body: JSON.stringify(body)
})
  .then(response => {
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    return response.json();
  })
  .then(data => {
    console.log(data);
  })
  .catch(error => {
    console.error('There was a problem with the fetch operation:', error);
  });
})

</script>